import React from "react"
import Container from "react-bootstrap/Container"

import { TextBlock } from "components/Text Block"
import { rsvp } from "content/RSVP"
import { Item } from "components/Item"

const extractFirstNamesAsString = (names) => {
    return names.map((name) => name.split(' ')[0]).join(", ");
}

const RSVPForm = ({ numberOfGuests, names }) => {

    const rsvpQuestions = rsvp

    const firstNames = extractFirstNamesAsString(names);
    const message = `${numberOfGuests} of us: ${firstNames}`;
    console.log("RSVP Message: "+message);

    return (
        <>
            <TextBlock
                text="RSVP">
            </TextBlock>
            <TextBlock
                text="Please email us with your response to the following questions to RSVP"
                textStyle={"p"}>
            </TextBlock>

            <Container fluid>
                {rsvpQuestions.map(
                    ({ID, Question}, idx) => (
                        <Item
                            key={idx}
                            LeftTitle={ID}
                            RightBody={Question}
                        />
                    )
                )}
            </Container>
        </>
    )
};

const AttendingView = () => {
    return (
        <>
            <TextBlock
                text="RSVP">
            </TextBlock>
            <TextBlock
                text={"Thank you for RSVP'ing. We are excited to have you at our wedding !!"}
                textStyle={"p"}>
            </TextBlock>
        </>
    )
};

const NotAttendingView = () => {
    return (
        <>
            <TextBlock
                text="RSVP">
            </TextBlock>
            <TextBlock
                text={"We are sorry you are unable to make it to our wedding. You will be missed 💔"}
                textStyle={"p"}>
            </TextBlock>
        </>
    )
};

const RSVP = ({ loginDetails }) => {

    let view;

    console.log(loginDetails);

    if (loginDetails.invitation.RSVP_STATUS === 'ATTENDING') {
        view = <AttendingView />;
    } else if (loginDetails.invitation.RSVP_STATUS === 'NOT_ATTENDING') {
        view = <NotAttendingView />;
    } else {
        view = <RSVPForm numberOfGuests={loginDetails.invitation.NUMBER_OF_GUESTS} names={loginDetails.invitation.NAMES} />;
    }

    return (
        <>
            {view}
        </>
    )
};

export default RSVP
